.milestone-component {
    .status-icon {
        height: 22px;
        width: 22px;
    }

    .video-play {
        background: linear-gradient(-45deg,#6336f1,#5f1de8);
        text-align: left;
        border-radius: 8px;

    }

    .video-play:active {
        background: linear-gradient(-45deg,#6336f1,#5f1de8);
    }

    .milestone-title {
        font-size: 15px;
        font-weight: bold;
    }

    .sub-milestone-title {
        font-size: 12px;
        color: #b6b5b4;
    }

    .play-button-icon {
        height: 25px;
        width: 25px;
        margin-right: 12px;
        margin-left: 17px;
    }

}