.course-information-component {
    background-color: #F5F7FF !important;
    padding-top: 50px;
    padding-bottom: 50px;
    .course-information-heading-div {
        padding: 13px;
        border-radius: 16px;
        background-color: white;
        width: 435px;
        margin-bottom: 50px;
    }

    p {
        font-size: 14px;
    }

    h5 {
        font-size: 17px;
    }

    .course-information-subtitle {
        margin: unset;
    }
    .course-information-title {
        color: white;
        padding: 13px;
        border-radius: 16px;
        background: linear-gradient(-45deg,#ff589b,#ff136f);
        width: 207px;
    }

    
}