.user-management-component {
    background-color: blue;
    width: 100%;
    .add-user-button {
        background-color: #E5F3FF;
        border: 1px solid #0084FF;
        box-sizing: border-box;
        border-radius: 5px;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #0084FF;
    }

    .search-text {
        width: 247px;
        height: 40px;
        background-color: #FFFFFF;
        border: 1px solid #BFBFBF;
        box-sizing: border-box;
        border-radius: 5px;
        padding-left: 38px;
    }

    .search-icon {
        height: 20px;
        position: absolute;
        right: 249px;
        top: 108px;
    }

    .state-title, .state-text {
        margin: unset;
    }

    .state-div {
        width: 195px;
        height: 80px;
        background: #FFFFFF;
        box-sizing: border-box;
        box-shadow: 0px 10px 20px rgba(0, 132, 255, 0.1);
        border-radius: 10px
    }

    .table-header {
        width: 1008px;
        height: 56px;
        background: #F5F6FA;
        border-radius: 10px;
    }
}