.module-video-component {
    div{
        width: 100% !important;
    }

    @media screen and (min-width: 768px) and (max-width: 990px) {
        div {
            margin-top: -67px;
        }

    }


    @media screen and (min-width: 991px) {
        div {
            margin-top: -27px;
        }
    }

    @media screen and (min-width: 1198px) {
        div {
            margin-top: 20px;
        }
    }
      
}