.notice{
    transform: translateY(-120px);
    .notice-wrapper{
        box-shadow: 0px 15px 20px 0px #3754aa1a;
        border-radius: 25px;
    }
    i{
        width: 35px;
        height: 35px;
        text-align: center;
        border-radius: 5px;
        color: $white;
        padding-top: 9px;
        margin-right: 15px;
        font-size: 16px;
    }
    .one{
        background-image: linear-gradient(-45deg, $blue, $light-blue);
    }
    .two{
        background-image: linear-gradient(-45deg, $tomato, $light-tomato);
    }
    .three{
        background-image: linear-gradient(-45deg, $yellow, $light-yellow);
    }
    .four{
        background-image: linear-gradient(-45deg, $pink, $light-pink);
    }
}