.sidebar-component {
    background-color: #111133;
    width: 307px;
    min-height: 900px;
    padding-top: 90px;

    .nav-link-text {
        font-size: 16px;
        line-height: 19px;
        vertical-align: top;
        padding-top: 0px;
        padding-bottom: 0px;
        margin: unset;
        margin-left: 7px;
    }

    li {
        height: 40px;
        width: 209px;
        background-color: #3A3A73;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .logo {
        height: 26px;
        margin-bottom: 30px;
    }

}