.course-criteria-section{
    .course-criterias{
        li{
            display: flex;
            margin-top: 30px;
            font-size: 17px;
            i{
                background: linear-gradient(75deg, $tomato, $light-tomato);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                margin-right: 8px;
                margin-top: 5px;

            }
        }
       
    }
}