.course-review-component {
    background-color: #F5F7FF;

    .course-review-rate {
        font-size: 188px !important;
    }

    .course-review-rating-star {
        margin-top: -50px;
    }

    .custom-progressbar-component {
        width: 300px !important;
    }

    .course-review-progressbar {
        margin-top: 68px;
    }

    .course-review-title {
        margin-top: 81px;
    }

    .course-review-div {
        margin-bottom: 81px;
    }

    .progress-bar {
        background: linear-gradient(45deg, #FF35B4, #FF193F,); 
        border-radius: 16px;
        height: 22px;
    }

    .progress-row {
        margin-bottom: 15px;
    }

    .progress {
        height: 22px;
        border-radius: 16px;
    }
      
}