.frequently-asked-component {
    .frequently-asked-main {
        background-image: url('../../images/frequently-asked-bg.png') !important;
        min-height: 667px;
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }

    .thinker-image {
        height: 418px;
        margin-top: 147px;
        margin-left: 44px;
    }

    @media screen and (max-width: 768px) {
        .frequently-asked-list-component {
            margin-top: 241px;
        }

        .thinker-image-div {
            text-align: center;
            margin-bottom: 77px;
        }

        .thinker-image {
            margin-top: 77px;
        }
    }
}