.result-component {
    .trophy {
        margin-top: 25px;
        height: 145px;
        width: 125px;
    }

    .status-div {
        height: 55px;
        width: 134px;
        border-radius: 14px;
        color: white;
    }

    .question-answered-div {
        margin-right: 12px;
        font-size: 28px;
        background-image: linear-gradient(-180deg, #fa6a57, #f68d6b);
    }

    .score-div {
        margin-left: 12px;
        font-size: 28px;
        background-image: linear-gradient(-180deg, #768192, #1ad2d7);
    }


    .score-title {
        margin-left: 12px;
        font-size: 14px;
    }

    .question-answered-title {
        margin-right: 12px;
        font-size: 14px;
    }

    .quiz-result-card {
        margin-top: 58px;
        border-radius: 12px;
    }

    .retake-button {
        height: 39px;
        width: 116px;
        color: white;
        margin-right: 14px;
        background-image: linear-gradient(-180deg, #6541f6, #5a00dd);
    }

    .next-button {
        height: 39px;
        width: 116px;
        color: white;
        margin-left: 14px;
        background-image: linear-gradient(-180deg, #ff4d94, #ff1470);
    }

}