$border-radius-base: 40px;

// tHEMING 
$theme-colors: (
  "primary":   $blue,
  "danger":    $pink,
);

body{
    color: #383838;
    font-family: 'Poppins', 'Hind Siliguri', sans-serif;
}


.francy-btn{
    padding: 15px 75px;
    border-radius: $border-radius-base;
    position: relative;
    white-space: nowrap;
}
.francy-btn::after {
    content: "\f061";
    position: absolute;
    right: 5px;
    top: 5px;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    height: 45px;
    width: 45px;
    background: #ffff;
    color: #f94f4f;
    border-radius: 50%;
    display: block;
    font-size: 1.4em;
    padding-top: 6px;
}

.btn-danger{
    background: linear-gradient(45deg, $pink, $light-pink);
    color: #fff;
    border: none;
}
.rounded{
    border-radius: 40px !important;
}

.bg-primary{
    background: linear-gradient(45deg, $blue, $light-blue);
}

.bg-danger{
    background: linear-gradient(45deg, $pink, $light-pink);
}

.box-shadow{
    box-shadow: 0px 0px 10px 10px #3754aa1a;
}

.shadow{
    box-shadow: 0px 15px 20px 0px #3754aa1a;
}
